import React from "react";
import { Link, graphql } from "gatsby";
import { rhythm, scale } from "../utils/typography";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBlog, faTags, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { genTags, retTagList } from "../utils/format";

import Bio from "../components/bio";
import Layout from "../components/layout";
import { Post, Tag } from "../components/index_components";
import SEO from "../components/seo";

import "../scss/index.scss";

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;
  const allPosts = data.allMarkdownRemark.edges;
  const numPosts = data.site.siteMetadata.settings.postsOnIndex;
  const numTags = data.site.siteMetadata.settings.tagsOnIndex;
  const latestPosts = allPosts.slice(0, numPosts);
  let tagList = allPosts.reduce((accumulator, post) => {
    const tags = genTags(post.node.frontmatter.tags);
    return retTagList(accumulator, tags);
  }, {});
  const sortedTagList = Object.entries(tagList).sort((a, b) => a[1] > b[1] ? -1 : 1);

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="Top" />
      <Bio />
      <h2
        style={{
          marginTop: rhythm(1),
          marginBottom: rhythm(1),
        }}
      >
        <FontAwesomeIcon icon={faBlog} style={{marginRight: "10px"}}/>
        Latest Posts
      </h2>
      <div className="index-page-post" style={{ marginLeft: rhythm(1) }}>
        {latestPosts.map(({ node }) => <Post {...node} key={node.fields.slug} />)}
      </div>
      <nav>
        <ul
          style={{
            listStyle: `none`,
            padding: 0,
            textAlign: "right"
          }}
        >
          <li className="index-link">
            <Link to="page1" rel="next">
              More Posts <FontAwesomeIcon icon={faArrowRight} style={{marginLeft: "10px"}}/>
            </Link>
          </li>
        </ul>
      </nav>

      <h2
        style={{
          marginTop: rhythm(1),
          marginBottom: rhythm(1),
        }}
      >
        <FontAwesomeIcon icon={faTags} style={{marginRight: "10px"}}/>
        Tags
      </h2>
      <div className="index-tag" style={{marginLeft: rhythm(1)}}>
        {sortedTagList.slice(0, numTags).map((tag, i) => <Tag tag={tag} key={i}/>)}
      </div>
      <nav>
        <ul
          style={{
            listStyle: `none`,
            padding: 0,
            textAlign: "right"
          }}
        >
          <li className="index-link" style={{marginTop: rhythm(0.5)}}>
            <Link to="tags" rel="next">
              More Tags <FontAwesomeIcon icon={faArrowRight} style={{marginLeft: "10px"}}/>
            </Link>
          </li>
        </ul>
      </nav>
    </Layout>
  );
};

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        settings {
          postsOnIndex
          tagsOnIndex
        }
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MM/DD")
            title
            description
            tags
          }
        }
      }
    }
  }
`;
